.mobile-nav-container
{
    position: fixed;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    padding-top: 80px;
    z-index: 990;

    .overlay
    {
        top:0;
        left: 0;
        transform: translate(0 , 0%);
        position: absolute;
        background-color: #fff;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    .mobile-nav
    {
        margin: auto;
        
        ul.mobile-nav-links
        {  
            padding: 0px;
            margin: 0px;
            text-decoration: none;
            list-style-type: none;
            margin: auto;
            display: flex;
            flex-direction: column;
            li
            {
                flex-grow: 1;
                padding: 10px 0;

                a{
                    color: #000 !important;
                }
            }
        }

        ul.social-nav-links
        {
            position: absolute;
            transform: translate(0 , -50%);
            padding: 0px;
            display: flex;
            bottom: 50px;
            left: 50%;
            transform: translateX(-50%);
            margin: 0 -8px;

            li 
            {
                border-radius: 100%;
                padding: 0 8px;

                font-size: 1.45rem;

                i::before
                {
                    color: #000;
                }    
            }
        }
    }
}



