

.tpi-links {
  width: 100%;
  margin: 10% 15%;

  span {
    font-family: "Oswald", sans-serif;
    font-size: 60px;
    font-weight: 900;
    position: absolute;
    cursor: pointer;
  }

  .first {
    left: 50vw;
    color: black;
  }&:hover{
      color: white;
     }

  .second {
    top: 38vh;
    left: 30vw;
    color: white;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #000000
  }

  .third {
    color: black;
    top: 60vh;
    left: 45vw;
  }

  .four {
    top: 80vh;
    left: 38vw;
    color: white;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #000000
  }

  @keyframes noise-anim{
    $steps:20;
    @for $i from 0 through $steps{
      #{percentage($i*(1/$steps))}{
        clip:rect(random(100)+px,9999px,random(100)+px,0);
      }
    }
  }
  .first:after{
    content:attr(data-text);
    position:absolute;
    left:2px;
    text-shadow:-1px 0 rgba(28, 0, 0, 0.25);
    top:0;
    color: #000000;
    background: #ffffff;
    overflow:hidden;
    clip:rect(0,900px,0,0);
    animation:noise-anim 2s infinite linear alternate-reverse;
  }
  .third:after{
    content:attr(data-text);
    position:absolute;
    left:2px;
    text-shadow:-1px 0 rgba(28, 0, 0, 0.25);
    top:0;
    color: #000000;
    overflow:hidden;
    clip:rect(0,900px,0,0);
    animation:noise-anim 2s infinite linear alternate-reverse;
  }



  @keyframes noise-anim-2{
    $steps:20;
    @for $i from 0 through $steps{
      #{percentage($i*(1/$steps))}{
        clip:rect(random(100)+px,9999px,random(100)+px,0);
      }
    }
  }
  .first:before{
    content:attr(data-text);
    position:absolute;
    left:-2px;
    text-shadow:1px 0 #fff1ef;
    top:0;
    color: #000000;
    background: #ffffff;
    overflow:hidden;
    clip:rect(0,900px,0,0);
    animation:noise-anim-2 3s infinite linear alternate-reverse;
  }
  .third:before{
    content:attr(data-text);
    position:absolute;
    left:-2px;
    text-shadow:1px 0 #fff1ef;
    top:0;
    color: #000000;
    overflow:hidden;
    clip:rect(0,900px,0,0);
    animation:noise-anim-2 3s infinite linear alternate-reverse;
  }
}

.social-links {
  img {
    position: absolute;
    width: 2.5%;
    left: 5.4vw;
    top: 30vh;
  }

  .facebook {
    top: 50vh;
  }
}