
// ===== colors ====== //

$palette : (
    white:white,
    off-black: #000000,
    coconut: #9B5C3B,
    brass: #C59B76,
    salmon: #E9806E,
    sand: #C0C781,
    green: #78BC61
);


// ===== properties (wrapper , margins\padding etc ====== // 

$props : (
    'wrapper' : 85%,
    'wrapper-max-width' : 1320  px,
);


$fonts :  (
    body: (min:14 , max:22),
    p: (min:14 , max:22),
    span: (min:14 , max:22),
    h1: (min:62 , max:115),
    h2: (min:48 , max:100),
    h3: (min:32 , max:65),
    h4: (min:28 , max:38),
    h5: (min:20 , max:28),
);