// ==== LOADER SETTINGS ==== //

.loader-container
{
    width: 200px;
    height: 200px;
    position: absolute;
    transform: translate(-50% , -50%);
    top: 50%;
    left: 50%;


    .loader-outer , .loader-inner , .loader-middle
    {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50% , -50%) rotate(45deg) scale(0.001);
        width: 100%;
        height: 100%;
        border: 3px solid #fff;

    }

    .loader-logo
    {
        position: absolute;
        top:50%;
        left: 50%;
        transform: translate(-50% , -50%);
    }
}