.h-100
{
    height: 100%;
}

.w-100
{
    width: 100%;
}

.wrapper 
{
    width: props("wrapper");
    max-width: 1920px;
    margin: 0 auto;

    @include media-breakpoint-down(sm)
    {
        width: 100%;
        padding: 0 20px;
    }
}


.no-padding
{
    padding: 0px !important;
}

.no-margin
{
    margin: 0px !important;
}


button
{
    border: transparent;
    &:focus,:hover
    {
        outline:none;
    }
}
.container-fluid 
{
    padding: 0px !important;
}

.usable 
{
    cursor: pointer;
}

// ---- text-styles ---- //



// ---- buttons ---- //

button.button
{
    border-color: transparent;
    padding: 0.2em 0.8em;
    &.white
    {
        background-color: #fff;
        color: #000;
    }

    &.rounded
    {
        border-radius: 1em;
    }


}


// ---- lists ---- //

ul
{
    &.no-style
    {
        li 
        {
            list-style-type: none;
        }
    } 
}

            
a
{
    color: #fff;
    text-decoration: none;
    &:hover
    {
        color: #fff;
        text-decoration: none;
    }


    &.black
    {
        color: #000;
        &:hover
        {
            color: #000;
        }
    }
}

// ---- components ---- //

.scroll-up-container
{
    width: 45px;
    height: 45px;
    background-color: #fff;
    position: fixed;
    bottom: 50px;
    right: 30px;

    &::before , &::after
    {
        content: "";
        display: block;
        position: absolute;
        width: 50%;
        height: 3px;
        background-color: #000;
        border-radius: 3px;
        top: 50%;
        left: 50%;
        transform: translate(-50% , -50%);
    }

    &::before
    {
        transform: rotate(45deg) translate(-50% , -50%);
    }

    &::after
    {
        transform: rotate(-45deg) translate(-50% , -50%);
    }
}


