

@import "node_modules/bootstrap/scss/functions";
@import "node_modules/bootstrap/scss/variables";
@import "node_modules/bootstrap/scss/mixins/_breakpoints";

header
{
    padding-top: 50px;
    z-index: 999;
    width: 100%;
    position: relative;
    background: transparent;
    overflow: hidden;

    &.fixed
    {
        position: fixed;
    }

    @include media-breakpoint-down(sm)
    {
        padding-top: 30px;
    }
    .logo-container {
        mix-blend-mode: overlay;
        img , a
        {
            mix-blend-mode: darken;
        }
    }

    // -- hamburger-menu -- //

    nav.primary-nav
    {
        ul
        {
            padding: 0px;
            margin: 0px -5px;

            li
            {
                list-style-type: none;
                padding: 0px 5px;
                position: relative;
            }

            a
            {
                color: #fff;
            }
        }
    }
    .menu-bars 
    {
        width: 30px;
        height: 25px;
        position: relative;
        .menu-bar 
        {
            width: 100%;            
            height: 2px;
            background-color: #fff;
            position: absolute;
            transition: 0.25s all;
            &:nth-child(1)
            {
                top: 0px;
            }

            &:nth-child(2)
            {
                top: 50%;
                transform: translateY(-50%);
                width: 50%;
                left: calc(100% - 6px); 

            }
            &:nth-child(3)
            {
                bottom: 0px;
            }
        }


        
        &:hover 
        {
            .menu-bar 
            {
                &:nth-child(2)
                {
                    left: -6px;
                    width: 75%;
                }
            }
        }


        &.active
        {
            .menu-bar 
            {
                &:nth-child(1) , &:nth-child(3)
                {
                    top: 50%;
                    transform: translateY(-50%);
                }

                &:nth-child(1)
                {
                    transform: rotate(45deg);
                }

                &:nth-child(3)
                {
                    transform: rotate(-45deg);
                }

                &:nth-child(2)
                {
                    left: 200% !important;
                    opacity: 0;
                }
            }
        }
    }


    &.dark
    {
        .logo
        {
            //TODO: change color here
        }

        .menu-bars
        {
            .menu-bar{
                background-color: #000;
            }
        }
    }



    // navigation glitch effect //
    .glitch-text-container
    {
       position: relative;
       
       .glitch-text-top , .glitch-text-bottom
       {
           position: absolute;
           width: 100%;
           overflow: hidden;
           visibility: hidden;
           left: 0px;
       }

       .glitch-text-bottom
       {
            height: 50%;
            bottom: 0px;
            line-height: 0px;
       }

       .glitch-text-top
       {
           height: 50%;
           top: 0px;
       }


       &.glitching
       {
           .glitch-text 
           {
               visibility: hidden;
           }
            .glitch-text-top , .glitch-text-bottom
            {
                visibility: visible;
                
            }
       }
    }

}