.form-container
{

    input , textarea
    {
        &:focus
        {
            outline: none;
        }

        color: #fff;

    }

    textarea
    {
        background-color: transparent;
        border: 1px solid #fff;
        color: #fff;
        padding: 10px 8px;
    }


    .input-group
    {
        textarea
        {
            transition: opacity 0.25s ease-in-out;
            opacity: 0.75;
        }

        &.active , &:hover
        {
            textarea
            {
                opacity:1;
            }
        }
    }
    .underline-input-group
    {
        height: auto;
        width: auto;
        position: relative;

        input
        {
            background: transparent;
            border: none;
            transition: all 0.25s;
            padding: 5px 2px;
            opacity: 0.75;
            width: 100%;
        }

        &::before, &::after
        {
            content: "";
            transition: 0.25s all;
        }
        &::before
        {
            display: block;
            position: absolute;
            height: 2px;
            width: 100%;
            background-color: gray;
            border-radius: 1px;
            bottom: 0px;
            left: 0px;
            z-index: 1;
        }

        &::after
        {
            display: block;
            position: absolute;
            height: 2px;
            width: 0%;
            background-color: #fff;
            border-radius: 1px;
            bottom: 0px;
            left: 50%;
            transform: translateX(-50%);
            z-index: 2;
        }


        &.active
        {
            &::after
            {
                width: 100%;
            }      
            
            input 
            {
                opacity: 1;
            }
        }

        &:hover
        {
            &::before
            {
                background-color: #eee;
            }

            input 
            {
                opacity: 1;
            }
        }


        &.validated
        {
            &::after
            {
                background-color: #fff;
            }
        }

        &.validation-error
        {
            input 
            {
                color: red !important;
            }
            &::after
            {
                background-color: red;
                width: 100% !important;
            }
        }
        
    }
}