.contact-us-container
{
    .contact-us
    {
        height: 100%;

        
        .contact-us-item-container
        {
            position: relative;
            height: 100%;
            width: 100%;
        }

        // --- any questions --- //


        .contact-us-item
        {  
            position: absolute;
            top: 50%;
            transform: translateY(-50%);

            &.position-normal
            {
                position: normal;
            }
        }
        .contact-us-item-footer
        {
            justify-self: flex-end;
            display: flex;
            align-items: center;
            padding: 25px 0px;
            margin: 0 -10px;
            a
            {
                padding: 0 10px;
            }

        }


        .contact-us-form
        {
            h2
            {
                margin-bottom: 30px;
            }
        }

        .contact-us-message
        {
            textarea
            {
                width: 100%;
                
            }
        }


        span.text-with-icon
        {
            display: flex;
            align-items: center;
            margin: 0 -4px;
            transition: margin 0.25s;

            .text 
            {
                transform: scale(0.75);
            }
            .text, .arrow
            {
                transition: all 0.25s;
                padding: 0 4px;
            }
            .arrow , .arrow-right
            {
                display: flex;
                align-items: center;
                .line
                {
                    display: block;
                    transition: width 0.25s;
                    width: 15px;
                    height: 2px;
                    background-color: #fff;
                }
                .icon
                {
                    font-size: 0.65em;
                }
            }


            &:hover
            {
                margin: 0 -8px;

                .text 
                {
                    transform: scale(1);
                }
                .text , .arrow
                {
                    padding: 0 8px;
                }

                .arrow
                {
                    .line
                    {
                        width: 30px;
                    }
                }
            }

            &.reverse
            {
                flex-direction: row-reverse;
            }
        }
    }


    .submit-message
    {
        display: block !important;
        position: initial !important;

       .contact-us-item
       {
           padding-top: 80px;

           display: block !important;
           position: initial !important;
           transform: none !important;


           .contact-us-item-group
           {
                padding: 20px 0;
           }

       }
        h5
        {
            font-size: 1.4rem;
        }
        .input-group  textarea
        {
            width: 100%;
            height: 220px;
        }

    }
}