// ===== NEW ONES ===== //
@import "node_modules/bootstrap/scss/functions";
@import "node_modules/bootstrap/scss/variables";
@import "node_modules/bootstrap/scss/mixins/_breakpoints";
.our-works-container
{
  
  overflow: hidden;
  @include media-breakpoint-down(xs) {
    .wrapper
    {
      padding: 0px !important;
      margin: 0px !important;
    }


    .our-work-pre-title,.our-work-pre-title , .our-work-name , .our-work-surname,
    .our-works-footer
    {
      padding: 15px 20px !important;
    }
  }
  .our-works
  {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding-top: 140px;

    .our-works-content 
    {
      position: relative;
      flex-grow: 12;

      .our-work 
      {
        height: 100%;
        .our-work-picture 
        {
          position: absolute;
          width: 80%;
          height: 100%;
          background-repeat: no-repeat;
          background-size: cover;
          page-break-inside: center center;
          bottom: 0px;
          right: 0px;
        }

        .our-work-pre-title
        {
          font-size: 1.2rem;
          font-weight: thin;
          color: #fff;
        }
        .our-work-meta
        {
          position: relative;
          height: 100%;          
          z-index: 5;
          .our-work-name , .our-work-surname
          {
            color: #fff;
          }

          .our-work-name
          {
            color: white;
            -webkit-text-fill-color: black; /* Will override color (regardless of order) */
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: white;
          }

          .our-work-surname
          {

          }
        }


        .our-work-next-button
        {
          background-color: #000;
          padding: 40px 75px;          
          color: #fff;
          position: absolute;
          right: 0px;
          bottom: 0px;
          display: flex;
          align-items: center;
          justify-content: center;

          @include media-breakpoint-down(sm)
          {
            padding: 20px 35px;
            font-size: 0.9em;
            text-transform: lowercase;
          }
        }
      }
    }

    .our-works-footer 
    {
      padding: 30px 0px;
      color: #fff;
      flex-grow: 1;


      .our-works-progress-bars
      {
        flex-wrap: no-wrap;
        width: 420px;
        margin: 0px -5px;
        margin: auto;


        @include media-breakpoint-down(sm) {
          width: 280px;
        }

        span
        {
          margin: 0px 8px;
        }

        .our-works-progress-bar-container
        {
          flex-grow: 1;
          min-height: 10px;
          padding: 0px 5px;
          display: flex;
          align-items: center;
          justify-content: center;


          .our-works-progress-bar-track
          {
            background-color: gray;
            width: 100%;
            height: 4px;
            position: relative;

            .our-works-progress-bar
            {
              position: absolute;
              background-color: #fff;
              height: 100%;
              width: 0%;
            }
          }
        }
      }
    }
  }
}