@import "node_modules/bootstrap/scss/functions";
@import "node_modules/bootstrap/scss/variables";
@import "node_modules/bootstrap/scss/mixins/_breakpoints";
@import "../../assets/functions/";
@import "../../assets/mixins/";


@keyframes mouseAnimation
{
   from
   {
        opacity: 0;
        top: 15%;
   }

   to
   {
     top: 60%;
     opacity: 1;
   }
}
.page.home-page
{

  section
  {
    margin-bottom: 20px;
  }
  .home-page-banner
  {
    position: relative;

     .__pre-title
     {
       white-space: nowrap;
       overflow: visible;
       transform: rotate(-90deg);

       @include media-breakpoint-down(sm) 
      {
       transform: none;
       margin: 8px 0;
      }
     }

     .__title 
     {
       margin: 0px;
       padding: 0px;
       line-height: 1em;
     }

     .background-picture
     {
       position: absolute;
       left: 0px;
       top: 0px;
       height: 75%;
       width: 65%;
       z-index: -1;
       background-position: center center;
       background-size: cover;
       background-repeat: no-repeat;


       
     }
     .social-media-links
     {
       position: absolute;
       bottom: 100px;
       left: 50%;
       transform: translateX(-50%);
       
       margin: 0 -8px;
       li
       {
         padding: 0 8px;
         width: 38px;

         a{
           width: 100%;
         }
         img
         {
           width: 100%;
           height: auto;
         }

       }
     }

     .mouse-scroll-down
     {
       width: 19px;
       height: 30px;
       border: 1px solid #fff;
       border-radius: 12px;
       position: relative;
       display: block;

       @include media-breakpoint-up(md)
       {
         display: none;
       }

       &::before
       {
         content: "";
         display: block;
         width: 4px;
         height: 4px;
         background-color: #fff;
         border-radius: 100%;
         position: absolute;
         left: 50%;
         top: 25%;
         transform: translate(-50% , -50%);
         animation: mouseAnimation 1.2s infinite;
       }

     }
  }


  .our-features-container
  {
    .wrapper
    {
      position: relative;
    }
    .our-features
    {
      .our-feature-item
      {
        width: 100%;
        padding: 15px 0px;

        .our-feature-item-picture
        {
          flex: 0 0 25%;
          padding: 0 15px;

        }

        .our-feature-item-description
        {
          @include media-breakpoint-down(sm)
          {
              text-align: center;
          }
        }
      }
    }


    .background
    {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
      z-index: -1;
      opacity: 0.10;

      .digit
      {
        position: absolute;
        font-size: 12em;
        &:nth-child(1)
        {
          top:30px;
          left: 40px;
        }

        &:nth-child(2)
        {
          bottom: 0px;
          left: -25px;
        }
      }

      .line
      {
        position: absolute;
        background-color: #fff;
        width: 2px;
        height: 100%;
        left: 8%;
      }
    }
  }
  


  .our-services-container
  {
    position: relative;
    padding: 80px 0;
    .title 
    {
      font-weight: normal;
      width: 85%;
      margin: 0 auto 100px auto;

      @include media-breakpoint-down(sm)
      {
        width: 100%;
        margin: 0 auto 25px auto;

      }
    }
    .services-wrapper
    {
      width: 50%;
      margin: 0 auto;

      @include media-breakpoint-down(sm)
      {
        width: 100% ;
      }
    }

    .background
    {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;

      .background-picture
      {
        position: absolute;
        top: 0;
        left:0;
        z-index: -1;
        width: 55%;
        height: 100%;
        background-position: center center;
        background-size: center center;
        background: no-repeat;
      }

      .digit
      {
        position: absolute;
        font-size: 12rem;
        right: 0px;
        opacity: 0.1;
        right: 45%;
        transform: translateX(50%);
        z-index: -2;

        &:nth-child(3)
        {
          right: 42%;
          top: 120px;
        }

        &:nth-child(2)
        {
          bottom: 0px;
          right: 30%;
        }
      }

    }
  }

  .watch-our-projects-container
  {
    position: relative;

    .watch-our-projects
    {
      button
      {
        border-radius: 30px;
        border:none;
        background-color: #fff;
        padding: 6px 10px;
      }
    }

    .background
    {
      z-index: -1;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50% , -50%);
      font-size: 12em;
      opacity: 0.10;
    }
  }

  .contacts-container
  {
    position: relative;
    .contacts
    {
      .contact-item
      {
          margin: 30px 0px;

          .social-media-links
          {
             
            margin: 0 -10px !important;
            width: 100%;
            .social-media-link
            {
              padding : 0px 10px;
            }
          }
      }
    }

    .contacts-picture
    {
      width: 100%;
      height: 100%;
      max-height: 680px;
      background-position: center center;
      background-size: cover;
      position: relative;

      &::before
      {
        content: "0";
        position: absolute;
        top: 80px;
        left: -65px;
        @include fluid-type(320px, 1300px, 85px, 225px);
        color: #fff;
      }

      &:after
      {
        content:"4";
        position: absolute;
        left: 80px;
        bottom: -120px;
        @include fluid-type(320px, 1300px, 85px, 225px);
      }
    }

    .contact-item
    {
      span
      {
        opacity: 0.5;
      }
    }
  }
}