.btcu-wrapper {
  width: 100%;
  background: #18191D;

  &--section-title {
    position: relative;

    &--image-1, &--image-2 {
      bottom: 0;
    }

    &--image-1 {
      position: absolute;
      left: 0;
    }

    &--image-2 {
      position: absolute;
      right: 0;
    }

    &--title {
      position: absolute;
      left: 40%;
      top: 10%;
      font-family: $portfolio-main;
      font-style: normal;
      font-weight: normal;
      font-size: 150px;
      line-height: 222px;
      letter-spacing: 0.07em;

      color: #FFFFFF;
    }
  }

  &--section-ideas {
    position: relative;
    &--title {
      position: absolute;
      top: 5%;
      left: 186px;

      p {
        color: #FFFFFF;
        font-family: $portfolio-main;
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        letter-spacing: 0.07em;
      }

      hr {
        width: 67px;
        height: 2px;
        background-color: #FFFFFF;
        border:none;
        margin-left: 34px;
      }
    }

    &--text {
      position: absolute;
      left: 186px;
      top: 20%;
      width: 651px;

      p {
        font-family: $portfolio-text;
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        line-height: 30px;
        letter-spacing: 0.07em;

        color: #8C8C8C;
      }
    }

    &--images {
      position: absolute;
      bottom: 20%;
      left: 5%;
      padding: 0 50px;
      width: 100%;
      height: 10%;
      img {
        height: 182px;
        width: 182px;
        position: unset;
        margin: 0 62px;
        display: inline-block;
      }
    }
  }

  &--section-colors {
    position: relative;
    display: flex;
    justify-content: space-between;

    &--circles, &--text {
      padding-top: 15%;
    }

    &--circles {
      padding-left: 125px;
      img {
        position: unset;
        width: 359px;
        height: 250px;
      }
    }

    &--text {
      padding-right: 125px;
      width: 427px;
      p {
      font-family: $portfolio-text;
      font-style: normal;
      font-weight: 300;
      font-size: 17px;
      line-height: 23px;

      color: #B8B8B8;
      }
    }

    &--background {
      position: absolute;
      left: 10%;
      bottom: 1%;

      font-family: $portfolio-main;
      font-size: 277px;
      line-height: 368px;

      color: rgba(214, 214, 214, 0.06);

      opacity: 0.16;
    }
  }

  &--section-typography {
    position: relative;
    display: flex;
    justify-content: space-between;

    &--font, &--text {
      padding-top: 15%;
    }

    &--font {
      padding-right: 125px;
      img {
        position: unset;
        width: 459px;
        height: 252px;
      }
    }

    &--text {
      padding-left: 125px;
      width: 449px;
      p {
        font-family: $portfolio-text;
        font-style: normal;
        font-weight: 300;
        font-size: 17px;
        line-height: 23px;

        color: #B8B8B8;
      }
    }

    &--background {
      position: absolute;
      left: 10%;
      bottom: 1%;

      font-family: $portfolio-main;
      font-size: 277px;
      line-height: 368px;

      color: rgba(214, 214, 214, 0.06);

      opacity: 0.16;
    }
  }

  &--section-illustrations {
    position: relative;
    background: white;
    &--title {
      position: absolute;
      top: 5%;
      left: 186px;

      p {
        color: #000000;
        font-family: $portfolio-main;
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        letter-spacing: 0.07em;
      }

      hr {
        width: 67px;
        height: 2px;
        background-color: #C4C4C4;
        border:none;
        margin-left: 34px;
      }
    }

    &--text {
      position: absolute;
      left: 186px;
      top: 20%;
      width: 472px;

      p {
        font-family: $portfolio-text;
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        line-height: 30px;
        letter-spacing: 0.07em;

        color: #757575;
      }
    }

    &--image {
      bottom: 5%;
      left: 20%;
    }
  }

  &--section-design-1 {
    width: 100%;

    img {
      position: unset;
      padding-left: 233px;
      padding-top: 100px;
    }
  }
  &--section-design-2, &--section-design-3 {
    width: 100%;
    display: flex;

    .white {
      background: #FFFFFF;
    }
    div {
      width: 50%;
      img {
        position: unset;
        padding-left: 124px;
        padding-top: 50px;
      }
    }
  }

  &--section-description {
    position: relative;
    background: white;
    &--title {
      position: absolute;
      top: 2.5%;
      left: 186px;

      p {
        color: #000000;
        font-family: $portfolio-main;
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        letter-spacing: 0.07em;
      }

      hr {
        width: 67px;
        height: 2px;
        background-color: #C4C4C4;
        border:none;
        margin-left: 34px;
      }
    }

    &--text {
      position: absolute;
      left: 186px;
      top: 10%;
      width: 472px;

      p {
        font-family: $portfolio-text;
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        line-height: 30px;
        letter-spacing: 0.07em;

        color: #757575;
      }
    }

    &--image-1 {
      right: 5%;
      top: 20%;
    }

    &--image-2 {
      left: 0;
      top: 21%;
    }

    &--image-3 {
      right: 0;
      top: 40%;
    }

    &--image-4 {
      right: 25%;
      top: 40%;
    }

    &--image-5 {
      left: 0;
      top: 41%;
    }

    &--image-6 {
      right: 0;
      bottom: 0;
    }

    &--image-7 {
      left: 2%;
      bottom: 0;
    }
  }

}