

$min_width : 320px;
$max_width : 680px;

$body: (min:18px , max:18px);
$p: (min:16px , max:18px);
$span: (min:16px , max:18px);
$h1: (min:42px , max:115px);
$h2: (min:48px , max:100px);
$h3: (min:32px , max:65px);
$h4: (min:24px , max:38px);
$h5: (min:20px , max:28px);


body
{
    color: #fff;
    background: black;
    font-family: Oswald;
    @include fluid-type($min_width, $max_width, map-get($body , "min"), map-get($body , max));


}

h1 
{
    @include fluid-type($min_width, $max_width, map-get($h1 , "min"), map-get($h1 , max));
}

h2
{
    @include fluid-type($min_width, $max_width, map-get($h2 , "min"), map-get($h2 , max));
}

h3
{
    @include fluid-type($min_width, $max_width, map-get($h3 , "min"), map-get($h3 , max));
}

h4
{
    @include fluid-type($min_width, $max_width, map-get($h4 , "min"), map-get($h4 , max));
}


h5
{
    @include fluid-type($min_width, $max_width, map-get($h5 , "min"), map-get($h5 , max));
}


p
{
    @include fluid-type($min_width, $max_width, map-get($p , "min"), map-get($p , max));
}


.text-center {text-align: center;}
.text-left {text-align: left;}
.text-right {text-align: right;}

.bold {font-weight: bold;}
.uppercase {text-transform: uppercase;}
.medium {}


.stroke
{
    color: transparent;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
}


