.nav-bar {
  margin: auto;
  max-width: 88.8%;
}

.nav-item {
  margin: 0 .5em;
  z-index: 2;

  .port {
    z-index: -1;
    width: 0;
    height: 20px;
    background: white;
    position: absolute;
    bottom: -.1vh;
    left: 0;
    overflow: hidden;
    -webkit-transition: width .5s ease-in-out;
    -moz-transition: width .5s ease-in-out;
    -o-transition: width .5s ease-in-out;
    transition: width .5s ease-in-out;
  }

  .port-1 {
    z-index: -1;
    width: 0;
    height: 20px;
    background: white;
    position: absolute;
    bottom: -.1vh;
    left: 8vw;
    overflow: hidden;
    -webkit-transition: width .5s ease-in-out;
    -moz-transition: width .5s ease-in-out;
    -o-transition: width .5s ease-in-out;
    transition: width .5s ease-in-out;
  }

  .port-2 {
    z-index: -1;
    width: 0;
    height: 20px;
    background: white;
    position: absolute;
    bottom: -.1vh;
    left: 17vw;
    overflow: hidden;
    -webkit-transition: width .5s ease-in-out;
    -moz-transition: width .5s ease-in-out;
    -o-transition: width .5s ease-in-out;
    transition: width .5s ease-in-out;
  }
}

.nav-item:hover {
  .port {
    width: 108px;
  }

  .port-1 {
    width: 38%;
  }

  .port-2 {
    width: 32%;
  }
}

.nav-logo {
  img {
    z-index: 999;
    top: 6vh;
  }
}

.nav-links {
  max-width: 25em;
  position: absolute;
  right: 1vw;
  font-family: "Oswald", sans-serif;
  font-size: 14.5px;
  margin-right: 3em;
  font-weight: 400;
  top: 7vh;
  -webkit-transition: width 1s ease-in-out;
  -moz-transition: width 1s ease-in-out;
  -o-transition: width 1s ease-in-out;
  transition: width 1s ease-in-out;
}

.nav-active {
  border-right: 1.5px solid white;
}

.nav-links a {
  color: white;
  text-decoration: none;
  padding: 0 .6px;
}

.nav-links a:hover {
  color: black;
  text-decoration: none;
}

.item-mid {
  margin-top: 10px;
  margin-left: 19px;
}

.item-bot {
  margin-top: 20px;
}

$burger-width: 1.5em;
$burger-distance: .5em;
$burger-thickness: 0.12em;
$burger-animation: 300ms;


.burger {
  width: $burger-width;
  margin: $burger-distance;
  position: relative;
  height: ($burger-thickness + $burger-distance) + $burger-thickness;
  cursor: pointer;
  margin-right: 2em;
  display: block;
  margin-top: .5em;
  z-index: 999;

  img {
    z-index: 999;
  }


  #top-black{
    background: black !important;
  }

  .top,
  .mid,
  .bot {
    height: $burger-thickness;
    background: #fefefe;
    position: absolute;
    width: $burger-width;
    left: 0;
    transition: all $burger-animation;
  }

  .top {
    margin: 0;
  }

  .mid {
    top: $burger-distance;
    transform: all $burger-animation / 2;
    width: $burger-width/2;
    left: 20px;
  }

  .bot {
    top: $burger-distance * 2;
  }

  &:hover {
    .mid {
      left: -1px;
    }

    .top {
      left: 3px;
    }

    .bot {
      left: 5.5px;
    }
  }


  .mid-active, .top-active, .bot-active{
    height: $burger-thickness;
    position: absolute;
    width: $burger-width+.3;
    left: 0;
    transition: all $burger-animation;
  }

  .mid-active {
    background: black;
    opacity: 0;
    width: 0;
  }

  .top-active,
  .bot-active {
    background: black;
    margin-top: $burger-width/3;
  }

  .top-active {
    transform: rotate(45deg);
  }

  .bot-active {
    transform: rotate(-45deg);
    top: 0;
  }


}
