.jambo-wrapper {
  width: 100%;

  img {
    position: absolute;
  }

  &--section-title {
    background: linear-gradient(-90deg, #F68E13 -0.01%, #FB0DD5 100%);

    &--title {
      font-family: $portfolio-main;
      margin: 0;
      font-size: 150px;
      line-height: 222px;
      color: white;
      position: absolute;
      left: 35%;
      top: 35%;
      z-index: 5;
    }

    &--svg-1, &--svg-2 {
      top: 0;
      left: 0;
    }

    &--svg-1 {
      z-index: 5;
    }

    &--svg-2 {
      z-index: 4;
    }

    &--svg-3, &--svg-4, &--svg-5, &--svg-6 {
      right: 0;
    }

    &--svg-3 {
      z-index: 6;
      bottom: -28%;
    }

    &--svg-4 {
      z-index: 5;
      bottom: -25%;
    }

    &--svg-5 {
      z-index: 4;
      bottom: -15%;
    }

    &--svg-6 {
      z-index: 3;
      bottom: -40%;
    }
  }

  &--section-content {
    position: relative;
    background: white;
    z-index: 10;

    &--subtitle-tasks, &--subtitle-concept, &--subtitle-adaptive {
      position: absolute;

      p {
        font-family: $portfolio-main;
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        letter-spacing: 0.07em;

        color: #000000;
      }

      hr {
        width: 67px;
        height: 2px;
        background-color: #C4C4C4;
        border:none;
        margin-left: 34px;
      }
    }

    &--subtitle-tasks {
      left: 165px;
      top: 142px;
    }

    &--subtitle-concept {
      left: 165px;
      top: 2545px;
    }

    &--subtitle-adaptive {
      left: 165px;
      top: 4438px;
    }

    &--text-tasks, &--text-concept, &--text-adaptive {
      margin-top: 20px;

      position: absolute;
      width: 472px;
      height: 213px;
      left: 165px;


      font-family: $portfolio-text;
      font-style: normal;
      font-weight: 300;
      font-size: 15px;
      line-height: 26px;
      letter-spacing: 0.07em;

      color: #757575;
    }

    &--text-tasks {
      top: 241px;
    }

    &--text-concept {
      top: 2644px;
    }

    &--text-adaptive {
      top: 4537px;
    }

    &--ellipse-1 {
      top: 363px;
      left: 0;
    }

    &--ellipse-2 {
      top: 494px;
      left: 138px;
    }

    &--ellipse-3 {
      top: 613px;
      left: 257px;
    }

    &--ellipse-4 {
      top: 740px;
      left: 384px;
    }

    &--ellipse-5 {
      top: 1309px;
      right: 0;
    }

    &--ellipse-6 {
      top: 1411px;
      right: 0;
    }

    &--screenshot-1 {
      top: 103px;
      right: 0;
    }

    &--screenshot-2 {
      top: 468px;
      left: 0;
    }

    &--screenshot-3 {
      top: 719px;
      right: 0;
    }

    &--screenshot-4 {
      top: 1088px;
      left: 0;
    }

    &--screenshot-5 {
      top: 1417px;
      right: 0;
    }

    &--screenshot-6 {
      top: 1794px;
      left: 0;
    }

    &--concept-ellipse-1 {
      top: 2624px;
      left: 828px;
    }

    &--concept-ellipse-2 {
      top: 2672px;
      left: 878px;
    }

    &--concept-ellipse-3 {
      top: 2694px;
      left: 900px;
    }

    &--concept-ellipse-4 {
      top: 2726px;
      left: 931px;
    }

    &--colors-1 {
      left: 1134px;
      top: 2714px;
    }

    &--group-1 {
      left: 199px;
      top: 2874px;
    }

    &--colors-2 {
      left: 595px;
      top: 2996px;
    }

    &--group-2 {
      left: 892px;
      top: 3149px;
    }

    &--group-3 {
      left: 0;
      top: 3438px;
    }

    &--font-title {
      position: absolute;
      left: 230px;
      top: 3680px;

      font-family: $portfolio-text;
      font-style: normal;
      font-weight: 500;
      font-size: 131px;
      line-height: 154px;

      color: #222222;
    }

    &--font-showcase {
      position: absolute;
      left: 485px;
      top: 3873px;
      width: 431px;

      font-family: $portfolio-text;
      font-style: normal;
      font-weight: 300;
      font-size: 22px;
      line-height: 41px;
      letter-spacing: 0.17em;

      color: #808080;
    }

    &--font-showcase-svg {
      left: 1060px;
      top: 3683px;
    }

    &--adaptive-ellipse {
      left: 0;
      width: 100%;
      top: 4384px;
    }

    &--adaptive-showcase {
      left: 0;
      top: 4536px;
      width: 100%;
    }
  }

}