.project-page
{
    width: 100%;
    height: auto;
    nav.project-nav-bar-container
    {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        z-index: 999;
        min-height: 65px;
        overflow: hidden;


        .project-nav-bar-visibility-icon
        {
            transform-origin: center center;
            transition: all 0.25s;
        }

        .project-nav-bar-inner-container
        {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0px;
            transition: all 0.25s;
            background-color: #000;
        }


        .project-nav-bar-visibility-icon-container
        {
            position: absolute;
            min-width: 50px;
            min-height: 65px;
            right: 0px;
            background-color:rgba(0,0,0,0.25);
            transition: all;

            .project-nav-bar-visibility-icon
            {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%) rotate(0deg);
            }
        }

        .project-nav-bar-controls
        {
            padding: 15px 0 !important;

            margin: 0 -8px !important;

            li
            {
                padding: 0 8px;
            }
        }


        .project-nav-bar-inner-container
        {
            position: absolute;
            width: 100%;
            height: 100%;
        }


        &.hidden
        {
            pointer-events: none;
            .project-nav-bar-inner-container
            {
                top: -100%;
            }

            .project-nav-bar-visibility-icon-container
            {
                min-width: 35px;
                min-height: 35px;
                pointer-events: all;

                .project-nav-bar-visibility-icon
                {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%) rotate(180deg);
                }
            }
        }

        .project-nav-bar-social-links
        {
            margin: 0 -10px !important;
            .project-nav-bar-social-link
            {
                padding: 0 10px;
            }
        }




    }


    .frame-container
    {
        position: relative;
        z-index: 991;
        width: 100%;
        iframe
        {
            width: 100%;
        }

        &.active-nav-bar
        {
            transition: all 0.25s;
            padding-top: 65px;
        }

        &.hidden-nav-bar
        {
            padding-top: 0px;
            transition: none;
        }



    }


    .project-preloader-container    
    {
        width: 100%;
        height: 100%;
        background-color: #000;
        position: absolute;
        z-index: 990;

        .project-preloader
        {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50% , -50%);
            height: 80px;
            width: 80px;

            img 
            {
                width: 100%;
            }
        }
    }

}